<template>
  <div>
    <div class="head">
      <van-icon name="search" />
      <van-nav-bar :title="list" />
      <van-icon name="user-o" @click="ren" />
    </div>
    <!-- <router-view></router-view> -->

    <van-swipe @change="onChange">
      <van-swipe-item>
        <van-grid square>
          <van-grid-item
            v-for="(item, index) in tu"
            :key="index"
            :icon="'https://fuss10.elemecdn.com' + item.image_url"
            :text="item.title"
            v-show="index < 8"
          ></van-grid-item>
        </van-grid>
      </van-swipe-item>
      <van-swipe-item>
        <van-grid square>
          <van-grid-item
            v-for="(item, index) in tu"
            :key="index"
            :icon="'https://fuss10.elemecdn.com' + item.image_url"
            :text="item.title"
            v-show="index >= 8"
          ></van-grid-item>
        </van-grid>
      </van-swipe-item>
    </van-swipe>

    <div class="shop">
      <p><van-icon name="shop-o" />附近商家</p>
      <ul>
        <li :key="index" v-for="(item, index) in fj">
          <img :src="'http://elm.cangdu.org/img/' + item.image_path" alt="" />
          <ol>
            <li class="li-top">
              <div>
                <p>品牌</p>
                <h2>{{ item.name }}</h2>
              </div>
              <span>保准票</span>
            </li>
            <li class="li-center">
              <img src="2c7ecd2126c7294ebfbc825b89217dfc.png" alt="" />
            </li>
          </ol>
        </li>
      </ul>
    </div>

    <van-tabbar v-model="active">
      <van-tabbar-item icon="phone-circle-o"
        >外卖</van-tabbar-item
      >
      <van-tabbar-item icon="browsing-history-o">搜索</van-tabbar-item>
      <van-tabbar-item icon="records">订单</van-tabbar-item>
      <van-tabbar-item icon="user-circle-o" @click="wo">我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  name: "",
  data() {
    return {
      list: "",
      active: 0,
      tu: [],
      fj: [],
    };
  },
  mounted() {
    this.list = this.$route.query.item;
    console.log(this.list);
    this.arr();
    this.fujin();
  },
  computed: {},
  methods: {
    onChange(index) {
      //   Toast("当前 Swipe 索引：" + index);
    },
    go() {
      this.$router.push("/find?list=" + list);
    },
    wo() {
      this.$router.push("/user");
    },
    ren() {
      this.$router.push("/user");
    },
    arr() {
      this.$http
        .get(
          "http://elm.cangdu.org/v2/index_entry?geohash=36.14952,120.389331&group_type=1&flags[]=F"
        )
        .then((res) => {
          console.log(res.data);
          this.tu = res.data;
        });
    },
    fujin() {
      this.$http
        .get(
          "http://elm.cangdu.org/shopping/restaurants?latitude=36.14952&longitude=120.389331&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]="
        )
        .then((res) => {
          console.log(res.data);
          this.fj = res.data;
        });
    },
  },
};
</script>

<style scoped lang="scss">
* {
  padding: 0;
  margin: 0;
}
.head {
  width: 100%;
  display: flex;
  justify-content: space-around;
  background: skyblue;
}
.van-nav-bar {
  width: 254px;
  background: skyblue;
}
.van-icon {
  font-size: 22px;
  margin-top: 10px;
}
.van-swipe {
//  margin-top: 48px;
  width: 100%;
  height: 200px;
  background: gold;
  /* padding-top: 180px; */
}
.shop {
  width: 100%;
  margin-top: 10px;
}

.shop {
  width: 100%;
  background: white;
  box-sizing: border-box;
  padding: 10px;
  > p {
    color: #999;
    font-size: 14px;
    text-align: left;
    margin: 0;
    display: flex;
    align-items: center;
    .van-icon {
      margin-right: 5px;
    }
  }
  > ul {
    > li {
      height: 100px;
    }
    li {
      display: flex;
      align-items: center;
      justify-content: space-between;
      // 右边部分
      ol {
        display: flex;
        flex-direction: column;
        flex: 1;
        box-sizing: border-box;
        padding-left: 20px;
        //    最上一层
        .li-top {
          div {
            display: flex;
            width: 86%;
            height: 20px;
            align-content: center;
          }
          p {
            font-size: 12px;
            font-weight: bold;
            width: 14%;
            height: 16px;
            background: #ffd930;
            margin: 0 10px 0 0;
          }
          h2 {
            font-size: 15px;
            margin: 0;
            width: 64%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            text-align: left;
          }
          span {
            font-size: 12px;
            color: #999;
          }
        }

        // 中间部分
        .li-center {
          margin-top: 10px;
          img {
            width: 100%;
            height: 36px;
          }
        }
        // 最下一层
      }
    }
    // 图片部分
    img {
      width: 60px;
      height: 60px;
    }
  }
}
</style>
