<template>
  <div>
    <!-- 头部布局 -->
    <header>
      <span class="sp1">ele.me</span>
      <div v-show="xian">
        <span @click="$router.push('/deng')">登录</span><span>|</span
        ><span>注册</span>
      </div>
      <div v-show="!xian">
        <van-icon name="https://b.yzcdn.cn/vant/icon-demo-1126.png" />
      </div>
    </header>
    <div class="head">
      <span class="sp0">当前定位城市:</span
      ><span class="sp">定位不准确，请在城市列表中选择</span>
    </div>

    <div class="nav">
      <span class="spa">{{ nav.name }}</span>
      <i class="el-icon-arrow-right"></i>
    </div>

    <div class="jiu">
      <div class="nei">
        <p>热门城市</p>
        <van-grid :column-num="4">
          <van-grid-item
            v-for="(item, index) in jiu"
            :key="index"
            :text="item.name"
             @click="$router.push(`/find?id=${item.id}&name=${item.name}`)"
          />
        </van-grid>
      </div>
    </div>

    <div class="pai">
      <van-index-bar>
        <div class="list" v-for="(item, index) in p" :key="index">
          <van-index-anchor :index="item" />
          <van-grid>
            <van-grid-item
              v-for="(it, index) in list[item]"
              :key="index"
              :text="it.name"
            />
          </van-grid>
        </div>
      </van-index-bar>
    </div>

<div class="ico" v-show="gotop" @click="toTop()">
  <van-icon class="icon1" name="arrow-up" />
</div>

  </div>

</template>

<script>
import bus from "../utils/evenutils";
export default {
  name: "",
  data() {
    return {
      nav: [],
      jiu: [],
      list: [],
      p: [],
      login: "",
      xian: true,
      gotop: false
    };
  },
  computed: {},
  mounted() {
    this.getNav();
    this.getJiu();
    this.getList();
    this.getLogin();

    　// 此处true需要加上，不加滚动事件可能绑定不成功
    window.addEventListener("scroll", this.handleScroll, true);

    bus.$on("xian", function (data) {
      console.log(data);
      this.xian = data;
      console.log(this.xian);
      if(this.login=""){
        this.xian=true
      }else{
        this.xian=false
      }
    });
  },
  methods: {
    getNav() {
      this.$http
        .get("http://elm.cangdu.org/v1/cities?type=guess")
        .then((res) => {
          //   console.log(res);
          //   console.log(res.data);
          //   console.log(res.data.name);

          this.nav = res.data;
          //   console.log(this.nav);
        });
    },
    getJiu() {
      this.$http.get("http://elm.cangdu.org/v1/cities?type=hot").then((res) => {
        // console.log(res);
        // console.log(res.data);
        this.jiu = res.data;
        console.log(this.jiu);
      });
    },
    getList() {
      this.$http
        .get("http://elm.cangdu.org/v1/cities?type=group")
        .then((res) => {
          console.log(res);
          console.log(res.data);
          this.list = res.data;
          console.log(this.list);
          this.p = Object.keys(this.list).sort();
          console.log(this.p);
        });
    },
    getLogin() {
      this.$http
        .get("http://elm.cangdu.org/v1/user?user_id=55514")
        .then((res) => {
          console.log(res);
          this.login = res.data.user_id;
          console.log(this.login);
        });
    },
    handleScroll() {
       let scrolltop = document.documentElement.scrollTop || document.body.scrollTop;
      scrolltop > 30 ? (this.gotop = true) : (this.gotop = false);
    },
    toTop() {
      
      let top = document.documentElement.scrollTop || document.body.scrollTop;
      // 实现滚动效果 
      const timeTop = setInterval(() => {
        document.body.scrollTop = document.documentElement.scrollTop = top -= 50;
        if (top <= 0) {
          clearInterval(timeTop);
        }
      }, 10);
    }
  },
};
</script>

<style scoped lang="scss">
* {
  padding: 0;
  margin: 0;
}

header {
  width: 100%;
  height: 40px;
  background: royalblue;
  color: seashell;
  display: flex;
  justify-content: space-between;
  text-align: center;
  z-index: 100;
  line-height: 40px;
  position: fixed;
  font-size: 18px;
  left: 0;
  top: 0;
}
.van-icon {
  margin-top: 10px;
  color:white;
}
.head {
  width: 100%;
  height: 32px;
  border-bottom: 1px solid rgb(163, 162, 162);
  padding-top: 40px;
}
.icon1{
  line-height: 38px;
  font-size: 40px;
  text-align: center;
  margin-left: 10px;
}
.sp0 {
  margin-right: 50px;
  font-size: 15px;
  color: darkgrey;
}
.sp {
  font-size: 13px;
  color: darkgrey;
}
.nav {
  width: 100%;
  height: 32px;
  text-align: center;
  line-height: 32px;
  border-bottom: 2px solid rgb(224, 222, 222);
}
.spa {
  color: rgb(92, 183, 240);
  margin-right: 310px;
  font-size: 20px;
}
.jiu {
  width: 100%;
  height: 160px;
  display: flex;
  align-items: center;
  background: rgb(241, 239, 239);
  line-height: 14px;
}
.nei {
  width: 100%;
  height: 130px;
  background: white;
  border-top: 2px solid rgb(224, 222, 222);
}
p {
  padding: 10px 0px;
}
.van-grid-item__text {
  color: rgb(92, 183, 240);
  z-index: 100px;
}
.list {
  width: 100%;
  border-bottom: 2px solid #b0b0b0;
}
.ico{
  width:60px;
  height:60px;
  background: darkgrey;
  border-radius: 50%;
  position: fixed;
  bottom: 20px;
  right:10px;
}
</style>
