<template>
  <div class="box">
    <van-nav-bar
      title="登录密码"
      left-text="返回"
      left-arrow
      @click-left="$router.go(-1)"
    />
    <div class="nei">
      <div class="de">
        <div class="divIn">
          <input type="text" placeholder="账号" v-model="account" />
        </div>
        <div class="divIn div1">
          <input type="text" placeholder="密码" v-model="password" /><input
            type="checkbox"
            class="switch"
          />
        </div>
        <div class="divIn div1">
          <input type="text" placeholder="验证码" v-model="pin" />
          <img :src="img" alt="" />
        </div>
        <p>温馨提示:为注册过得账号，登录时要将自动注册</p>
        <p>注册过的用户可凭账号账号密码登录</p>
        <button v-show="(is = false)" disabled>登录</button>
        <button @click="add()" v-show="(is = true)">登录</button>
      </div>
    </div>
  </div>
</template>

<script>
import bus from "../utils/evenutils";
export default {
  name: "",
  data() {
    return {
      account: "",
      password: "",
      pin: "",
      is: false,
      img: [],
      user_id: "",
      xian:false,
      code:"",
    };
  },
  mounted() {
    this.getImg();
  },
  computed: {},

  methods: {
    add() {
      if (
        !/^1[3456789]\d{9}$/.test(this.account) ||
        this.password == "" ||
        this.pin==""||this.pin==!this.img
      ) {
        this.is = false;
      } else {
        this.is = true;

        console.log(event.target);
        this.$router.push("/");
        bus.$emit("xian",this.xian)
      }
    },
    getImg() {
      this.$http.post("http://elm.cangdu.org/v1/captchas").then((res) => {
        console.log(res);
        this.img = res.data.code;
        this.code=res.headers
        console.log(this.code)
        console.log(this.img);
      });
    },
  },
};
</script>

<style scoped lang="scss">
html body {
  width: 100%;
  height: 100%;
}
.van-nav-bar__title {
  color: #1989fa;
}
.box {
  width: 100%;
  height: 100%;
  background: rgb(241, 241, 241);
}
.nei {
  width: 100%;
  height: 100%;
}
.divIn {
  border: 1px solid #dddddd;
  background: white;
}
input {
  width: 100%;
  height: 60px;
  border: none;

  ::placeholder {
    font-size: 30px;
  }
}
.div1 {
  display: flex;
  justify-content: space-between;
}
.de {
  margin: 10px 0px;
  button {
    width: 100%;
    height: 45px;
    background: seagreen;
    color: white;
    border: 1px solid seagreen;
    margin-bottom: 10px;
    border-radius: 5px;
  }
}
p {
  color: tomato;
}
/* Switch开关样式 */
input[type="checkbox"].switch {
  outline: none;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  position: relative;
  width: 40px;
  height: 20px;
  background: #ccc;
  border-radius: 10px;
  transition: border-color 0.3s, background-color 0.3s;
}
.switch {
  margin-top: 22px;
}

input[type="checkbox"].switch::after {
  content: "";
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0, 0, 2px, #999;
  transition: 0.4s;
  top: 2px;
  position: absolute;
  left: 2px;
}

input[type="checkbox"].switch:checked {
  background: rgb(19, 206, 102);
}

/* 当input[type=checkbox]被选中时：伪元素显示下面样式 位置发生变化 */
input[type="checkbox"].switch:checked::after {
  content: "";
  position: absolute;
  left: 55%;
  top: 2px;
}
</style>
