<template>
  <div>
    <van-nav-bar
      :title="name"
      left-text="返回"
      right-text="切换城市"
      left-arrow
      @click-left="$router.push('/')"
    />
    <input type="text" placeholder="搜索城市" v-model="keyword" />
    <span class="spa1" v-show="!del" @click="dele()">x</span>
    <button @click="ti()">提交</button>
    <div class="allDel"><p>搜索历史</p><span @click="allDel()">删除所有</span></div> 
    <div>
       <ol>
      <li v-for="(item, index) in List" :key="index">{{ item }}</li>
    </ol>
    </div>
   

    <ul v-show="!Is">
      <li v-for="(item, index) in search" @click="his(item.name)" :key="index">
       <p>{{ item.name }}</p> 
        <p>{{item.address}}</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      city: [],
      id: "",
      name: "",
      keyword: "",
      list: [],
      Is: true,
      del: true,
      history: "",
      List: [],
    };
  },
  mounted() {
    this.id = this.$route.query.id;
    this.name = this.$route.query.name;
    console.log(this.id, this.name);
    this.history = this.$route.query.list;
    console.log(this.history);

    this.get();

    this.getCity();
    this.getDD();
  },
  computed: {
    search() {
      if (this.keyword == "") {
        return [];
      } else {
        return this.list.filter((item) => {
          return item.name.indexOf(this.keyword) > -1;
        });
      }
    },
  },
  watch: {
    keyword(res) {
      console.log(res);
      if (res == "") {
        this.Is = true;
        this.del = true;
        console.log(this.Is);
        console.log(this.del);
      } else {
        this.del = false;
      }
    },
  },
  methods: {
    getCity() {
      this.$http.get("http://elm.cangdu.org/v1/cities?type=hot").then((res) => {
        // console.log(res);
        // console.log(res.data);
        this.city = res.data;
        console.log(this.city);
      });
    },
    getDD() {
      this.$http
        .get(
          `http://elm.cangdu.org/v1/pois?type=search&city_id=${this.id}&keyword=${this.name}`
        )
        .then((res) => {
          console.log(res.data);
          this.list = res.data;
          console.log(this.list);
        });
    },
    ti() {
      this.Is = false;
      console.log(this.Is);
    },
    dele() {
      this.keyword = "";
    },
    his(item) {
      this.$router.push("/detail?item=" + item);
       //  添加历史记录
      this.List.unshift(this.name);
      // this.name = "";
      // 将历史纪录添加到本地存储
      localStorage.setItem("list", JSON.stringify(this.List));
    },
    get() {
      //     /获取历史记录
      let data = localStorage.getItem("list");
      if (data == null) {
        this.List = [];
      } else {
        this.List = JSON.parse(data);
      }
    },
    allDel(){
      localStorage.removeItem("list");
      this.get()
    }
  },

 

};
</script>

<style scoped>
*{
  padding: 0;
  margin:0;
}
input {
  width: 100%;
  height: 40px;
  border: 1px solid #dddddd;
  border-radius: 5px;
  position: relative;
}
.spa1 {
  position: absolute;
  right: 16px;
  top: 60px;
}
ol{
  flex-wrap: wrap;
}
button {
  width: 100%;
  height: 35px;
  background: cadetblue;
  color: white;
  border-radius: 5px;
  border: 1px solid cadetblue;
  margin-top: 5px;
}
ul li{
  width:100%;
  height:60px;
  border-bottom: 5px;
  border:1px solid #dddd;
  margin-top: 5px;
}
ul{
  margin-top: 10px;
}
ol li{
  width:60px;
  height:30px;
  text-align: center;
  line-height: 30px;
  border:1px solid #dddddd;
  margin-left: 5px;
}
ol{
  display: flex;
}
.allDel{
  display: flex;
  justify-content: space-between;
}
.allDel span{
  margin-top: 10px;
}

</style>
